@use '../../partials/globals' as *;
@use '../../partials/variables';
@use '../../partials/mixins' as *;
@use '../../partials/typography' as *;

.shipping {
  @include background;
}

.submit {
  padding: 0 2rem;
  max-width: 50rem;
  margin: 0 auto;
  &__title {
    @include M-bebas-header;
    color: white;
    text-align: center;
    padding: 2rem 0;

    @include tablet {
      @include T-bebas-header;
      color: white;
    }
  }

  .inputs {
    &__label {
      @include label;
    }

    &__input {
      @include input;
    }
  }

  .continueBtn {
    @include button;
    margin-top: 1rem;
    border: 1px solid $primary-dark-blue;
    transition: 0.2s;
    &:hover {
      color: $primary-light-blue;
      border: 2px solid $primary-light-blue;
    }
  }
}
